@import url('https://fonts.googleapis.com/css2?family=Noto+Serif+SC:wght@400;500&display=swap');
@font-face {
  font-family: Realtime;
  src: url('./realtime-text-rounded.otf') format('opentype');
}

@font-face {
  font-family: Realtime-semibold;
  src: url('./realtime-text-semibold.otf') format('opentype');
}

@font-face {
  font-family: Realtime-bold;
  src: url('./realtime-text-bold.otf') format('opentype');
}

.App {
  width: 100%; /* For small screens */
  max-width: 1140px; /* For large screens */
  margin: auto; /* Center the container in the viewport */
  // font-family: -apple-system,BlinkMacSystemFont,Segoe UI,Roboto,Oxygen,Ubuntu,Cantarell,Fira Sans,Droid Sans,Helvetica Neue,sans-serif;
  font-family: 'Realtime-semibold', sans-serif;
  font-weight: 400;
  display: flex;
  flex-direction: column;
  background-repeat: no-repeat;
  background-attachment: fixed;
  background-position: 100% 1%;
}

h1 {
  font-size: 1.912em;
  line-height: calc(18px + 1.8vw); /* Responsive Vertical Rhythm */
}

h2 {
  font-size: 1.616em;
  line-height: calc(18px + 1vw); /* Responsive Vertical Rhythm */
}

h3 {
  font-size: 1.471em;
  line-height: calc(18px + 0.7vw); /* Responsive Vertical Rhythm */
}

h4 {
  font-size: 1.3em;
}
h5 {
  font-size: 1.243em;
}
h6 {
  font-size: 1.132em;
}

h4,
h5,
h6 {
  line-height: calc(18px + 0.2vw); /* Responsive Vertical Rhythm */
}

h1,
h2,
h3,
h4,
h5,
h6 {
  margin: calc(12px + 1.05vw) 0; /* Responsive margins */
}
