@import 'assets/styles/HelperFunctions';

.Nav {
  font-family: 'Realtime-bold', sans-serif;
  position: fixed;
  top: 0px;
  left: 0px;
  height: 60px;
  box-sizing: border-box;
  padding: 0 6vw 0 4.64vw;
  width: 100%;
  z-index: 10;
  transition: 0.4s ease-in-out 0.3s;
  color: rgb(5, 5, 5);
  background: rgb(250, 250, 250);
  border-bottom: 1px solid rgb(242, 242, 242);
  @include sm {
    height: 80px;
    background: #ffff;
    border-bottom: none;
  }
  display: flex;
  align-items: center;
  justify-content: space-between;
  .logo {
    text-decoration: none;
    color: rgb(51, 51, 51);
    //text-transform: uppercase;
    font-size: 20px;
    line-height: 24px;
    font-weight: 400;
    @include sm {
      color: #222;
      font-size: 24px;
      font-weight: 400;
    }
    &::after {
      content: '◦◦◦';
      margin-left: 0.3em;
      font-size: 12px;
    }
  }
  nav {
    display: inline-block;
    a.nav-item {
      text-decoration: none;
      text-transform: capitalize !important;
      font-size: 15px;
      line-height: 20px;
      margin-left: 1.5em;
      color: black;
      // @include sm {
      //   color: black;
      //   font-weight: 500;
      //   font-size: 14px;
      //   margin-left: 1.5em;
      //   letter-spacing: 0.7px;
      // }

      &.active {
        // color: rgba(0, 0, 0, 0.8);
        border-bottom: 2px solid black;
      }
    }
  }
}

.Nav-hidden {
  top: -60px;
  @include sm {
    top: -80px;
  }
}

.Nav-box-shadow {
  box-shadow: 0 4px 10px -10px rgba(0, 0, 0, 0.6);
}

.transparent {
  color: rgb(5, 5, 5);
  background-color: transparent;
  border-bottom: none;
  nav {
    font-size: 16px;
  }
}
